.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f0f0f0;
}

.dropdown-container {
  margin-bottom: 20px;
}

.input-container {
  margin: 3px;
  margin-top: 70px;
  padding: 3px;
  border: 1px solid #22333B;
  border-radius: 5px;
  background-color: #22333B;
  width: 32%;
  text-align: center;
  box-sizing: border-box;
  transition: transform 0.2s;
}

.input-container:hover {
  transform: scale(1.05);
}

.input-box {
  padding: 3px;
  margin-bottom: 3px;
  font-size: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 90%;
  text-align: center;
  background-color: white;
  color: white;
}

.input-box::placeholder {
  color: white;
  opacity: 0.7;
}

.top-input {
  background-color: #0A3D91;
}

.bottom-input {
  background-color: #B22222;
}

.team-data-container {
  text-align: center;
  background-color: #22333B;
  border-radius: 5px;
  padding: 3px;
  margin-top: 0;
  border: 1px solid #4d5255;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-header {
  font-weight: bold;
  margin-top: 3px;
  margin-bottom: 3px;
  font-size: 17px;
  color: #E0E0E0;
}

.grid-container {
  display: flex;
  flex-direction: column;
  gap: 1px;
}

.grid-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  padding: 2px 5px;
  background-color: #1BB8CC;
  font-size: 15px;
  color: #22333B;
  border-radius: 3px;
  width: 250px;
}

#heatmapContainer {
  margin-top: 20px;
  position: relative;
  width: 1000px;
  height: 500px;
  transition: transform 0.2s;
}

#heatmapContainer:hover {
  transform: scale(1.05);
}

.spinner-container {
  position: fixed;
  bottom: 10px;
  left: 10px;
  z-index: 1000;
}

.spinner {
  border: 8px solid #f3f3f3;
  border-top: 8px solid #3498db;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
